

.container{
width: 100%;
}
.logoContainer{
    width: 100%;
    position: absolute;
    top: 25%;
    text-align: center;
    font-size: 90px;
    font-family: "Roboto", Helvetica;
    font-style: italic;
    font-weight: 700;
    color:#2455C5;
}

@media only screen and (max-width: 768px) {
  .logoContainer {
    font-size: 70px;
  }
}

.welcomesearchrow{
display: flex;
position:absolute;
top: 50%;
width:60%;
margin-left:20%;
margin-right:20%;
border-radius: 30px;
border: 1px solid #D3D3D3;
}

.welcomesearchrow:focus-within{
box-shadow: 0 0 5px 0px #D3D3D3;
}

.subText{
width: 100%;
position: absolute;
top: 42%;
text-align: center;
font-size: 25px;
font-family: "Roboto", Helvetica;
font-weight: 700;
}

.bottomText{
width: 100%;
position: absolute;
top:70%;
text-align: center;
font-size: 15px;
font-family: "Roboto", Helvetica;
}

.bottomText a{
color: black;
text-decoration: none;
}

.projectinfo{
width: 100%;
position: absolute;
top: 80%;
text-align: center;
font-size: 20px;
font-family: "Roboto", Helvetica;
font-weight: 700;
white-space: pre;
}

.searchbarcontainer {
  display: flex;
  padding:5px;
  height: 100%;
}

.mainsearchinput {
   padding-left: 10px;
   width: 100%;
   height:30px;
   font-size: 16px;
   background: none;
   outline: none;
   border: none;
   border-radius: 6px;
}

.mainsearchinput::placeholder {
  text-align: center;
}

.searchbarcol{
display: flex;
flex-direction: column;
width: 100%;
padding: 5px;
}

.gobuttoncol{
font-size:20px;
padding-top: 7px;
padding-right:  10px;
}

.searchbarrow{
display: flex;
flex-direction: column;
}

.GoButton {
  justify-content: center;
  border: none;
  background: #FFFFFF;
  font-size: 20px;
  background: none;
}

.userProfileContainer{
position: absolute;
top: 2%;
right: 2%;
}